
.column{
height:528px;
 padding:0 33px 0 8px;
}
.column_test{
  width:100%;
  margin-top:30px;
  
  font-size:0;
  overflow:hidden;
}
.column_test span {
  width:25%;
  text-align:center;
  display:block;
 float:left;
 height:34px;
  padding:8px 0px;
  color:#5a626a;
  background:#b2c8dd;
  font-size:14px;
}
.column_selemnt{
  width:100%;
  overflow:hidden;
}
.column_selemnt > div{
width:25%;
 float:left;
 text-align:center;
 padding-top:16px;
}
.column_ico span {
  margin-right:6px;
  font-size:14px;
}
.active{
color:#c7dae4
}



