
.layout{
        border: 1px solid #d7dde4;
        background: #7dc9fb;
        position: relative;
        border-radius: 4px;
        padding:0 42px 0 21px;
}
.layout-header{
        height: 60px;
        background: #fff;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
}
.layout-copy{
        text-align: center;
        padding: 10px 0 20px;
        color: #9ea7b4;
}
.layout-ceiling{
        background: #7dc9fb;
        padding: 8px 0;
        overflow: hidden;
}
.layout-ceiling-Left{
        float: left;
        margin-right: 15px;
}
.layout-ceiling-main{
        float: right;
        margin-right: 15px;
}
.layout-ceiling-Left a,
    .layout-ceiling-main a{
      display: inline-block;
        color: #fff;
        font-size:14px;
       vertical-align: text-top;
      line-height:40px;
}
.layout-ceiling-main a + a {
      margin-left:36px;
}
.NameUsers img{
    width:25px;
    height:25px;
    border-radius:25px;
    position: relative;
    top:8px;
}
.NameUsers i{
    font-style: normal;
    vertical-align:top;
}



